.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.container {
    width: 300px;
    height: 250px;
    border: 1px solid #fff;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.3);
    /*textAlign: center;*/
}

.containerForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #fff;

}

.wrapperHeader {
    font-size: 1.5em;
    color: #fff;
}

.containerFormInput, .field {
    margin: 3px;
    text-align: center;
}

.field{
    width: 250px;
    height: 30px;

}

.noError {
    opacity: 0;
}

.formerror {
    color: #07f5e2;
}